import './index.scss';

import classNames from 'classnames';
import Icon from 'components/ui/Icon';
import { ChangeEvent, InputHTMLAttributes, memo, useId } from 'react';
import { Controller, useFormContext } from 'react-hook-form';

// ----------------------------------------------------------------------

/**
 * hasContext - form 사용 여부
 */
export type CheckboxProps = {
  name: string;
  label?: string;
  size?: 'lg' | 'md' | 'sm' | 'xs';
  color?: 'primary' | 'secondary';
  checked?: boolean;
  reverse?: boolean;
  hasContext?: boolean;
  onChange?: (e: ChangeEvent<HTMLInputElement>) => void;
} & Omit<InputHTMLAttributes<HTMLInputElement>, 'size'>;

const SIZE_MAPS = {
  lg: 'qb-checkbox--lg',
  md: 'qb-checkbox--md',
  sm: 'qb-checkbox--sm',
  xs: 'qb-checkbox--xs'
};

const COLOR_MAPS = {
  primary: 'qb-checkbox--primary',
  secondary: 'qb-checkbox--secondary'
};

export const ContextCheckbox = ({
  name,
  id,
  size = 'md',
  color = 'primary',
  disabled,
  reverse,
  label,
  checked,
  defaultChecked
}: CheckboxProps) => {
  const { control } = useFormContext();

  return (
    <Controller
      name={name}
      control={control}
      render={({ field: { ref, ...fieldRest } }) => {
        return (
          <div
            className={classNames(
              'qb-checkbox',
              SIZE_MAPS[size],
              COLOR_MAPS[color],
              {
                'qb-checkbox--disabled': disabled
              },
              {
                'qb-checkbox--reverse': reverse
              }
            )}
          >
            <input
              type="checkbox"
              id={id}
              className="qb-checkbox__form focus-visible--all"
              ref={ref}
              disabled={disabled}
              checked={checked}
              defaultChecked={defaultChecked}
              {...fieldRest}
            />
            <Icon name="icon-check-check2" className="qb-checkbox__icon" />
            {label && (
              <label htmlFor={id} className="qb-checkbox__label">
                {label}
              </label>
            )}
          </div>
        );
      }}
    />
  );
};

const Checkbox = ({
  name,
  label,
  size = 'md',
  color = 'primary',
  reverse = false,
  hasContext = false,
  disabled,
  checked,
  defaultChecked = false,
  onChange
}: CheckboxProps) => {
  const id = useId();

  return (
    <>
      {hasContext ? (
        <ContextCheckbox
          name={name}
          id={id}
          size={size}
          color={color}
          checked={checked}
          reverse={reverse}
          label={label}
          defaultChecked={defaultChecked}
        />
      ) : (
        <div
          className={classNames(
            'qb-checkbox',
            SIZE_MAPS[size],
            COLOR_MAPS[color],
            {
              'qb-checkbox--disabled': disabled
            },
            {
              'qb-checkbox--reverse': reverse
            }
          )}
        >
          <input
            type="checkbox"
            name={name}
            id={id}
            disabled={disabled}
            className="qb-checkbox__form focus-visible--all"
            checked={checked}
            defaultChecked={defaultChecked}
            onChange={onChange}
          />
          <Icon name="icon-check-check2" className="qb-checkbox__icon" />
          {label && (
            <label htmlFor={id} className="qb-checkbox__label">
              {label}
            </label>
          )}
        </div>
      )}
    </>
  );
};

const MemoizedCheckbox = memo(Checkbox);
export default MemoizedCheckbox;
