import './index.scss';

import classNames from 'classnames';
import ErrorMessage from 'components/forms/ErrorMessage';
import Label from 'components/forms/Label';
import Icon from 'components/ui/Icon';
import _get from 'lodash/get';
import { FocusEventHandler, FormEventHandler, ReactNode, memo } from 'react';
import { Controller, useFormContext } from 'react-hook-form';

// ----------------------------------------------------------------------

export type TextareaProps = {
  name: string;
  placeholder: string;
  disabled?: boolean;
  readOnly?: boolean;
  label?: ReactNode;
  value?: string | number;
  defaultValue?: string | number;
  defaultHeight?: number;
  required?: boolean;
  flexDirection?: 'row' | 'column';
  variant?: 'bordered' | 'ghost';
  size?: 'lg' | 'md' | 'sm' | 'xs';
  align?: 'left' | 'center' | 'right';
  color?: 'primary' | 'secondary';
  clearable?: boolean;
  autoComplete?: 'on' | 'off';
};

const COLOR_MAPS = {
  primary: 'qb-textarea--primary',
  secondary: 'qb-textarea--secondary'
};

const VARIANT_MAPS = {
  bordered: 'qb-textarea--bordered',
  ghost: 'qb-textarea--ghost'
};

const SIZE_MAPS = {
  lg: 'qb-textarea--lg',
  md: 'qb-textarea--md',
  sm: 'qb-textarea--sm',
  xs: 'qb-textarea--xs'
};

const ALIGN_MAPS = {
  left: 'qb-textarea--left',
  center: 'qb-textarea--center',
  right: 'qb-textarea--right'
};

const Textarea = ({
  name,
  placeholder,
  disabled = false,
  readOnly = false,
  label,
  defaultValue,
  defaultHeight,
  required = false,
  flexDirection = 'column',
  variant = 'bordered',
  size = 'md',
  align = 'left',
  color = 'primary',
  clearable = false,
  autoComplete = 'off',
  ...props
}: TextareaProps) => {
  const { control, reset, formState } = useFormContext();

  const errorMessage = _get(formState?.errors, `${name}.message`, '') as string;

  return (
    <Controller
      name={name}
      control={control}
      defaultValue={defaultValue}
      render={({ field: { ref, ...fieldRest } }) => {
        const handleResizeHeight: FormEventHandler<HTMLTextAreaElement> = (e) => {
          const element = e.target as HTMLTextAreaElement;
          element.style.height = 'auto';
          element.style.height = `${element.scrollHeight}px`;
        };

        const onFocus: FocusEventHandler<HTMLTextAreaElement> = (e) => {
          const { target } = e;
          setTimeout(() => {
            target.style.height = `${target.scrollHeight}px`;
          }, 0);
        };

        return (
          <div
            className={classNames(
              'qb-textarea',
              {
                'qb-textarea--row': flexDirection === 'row'
              },
              {
                'qb-textarea--disabled': disabled
              },
              {
                'qb-textarea--readonly': readOnly
              },
              {
                'qb-textarea--clearable': clearable
              },
              VARIANT_MAPS[variant],
              SIZE_MAPS[size],
              ALIGN_MAPS[align],
              COLOR_MAPS[color]
            )}
          >
            {label && <Label text={label} required={required} />}
            <div className="qb-textarea__outside">
              <div className="qb-textarea__actions">
                <textarea
                  className="qb-textarea__form"
                  style={{
                    height: defaultHeight
                  }}
                  rows={1}
                  onInput={handleResizeHeight}
                  onFocus={onFocus}
                  ref={ref}
                  placeholder={placeholder}
                  disabled={disabled}
                  readOnly={readOnly}
                  autoComplete={autoComplete}
                  {...fieldRest}
                  value={fieldRest.value || ''}
                />
                {clearable && fieldRest.value !== '' && !disabled && !readOnly && (
                  <button
                    type="button"
                    className="qb-textarea__clearable"
                    onClick={() => {
                      reset({
                        [name]: ''
                      });
                    }}
                  >
                    <Icon
                      name="icon-basic-close"
                      size="text-20"
                      color="text-gray-600 hover:text-gray-700"
                    />
                  </button>
                )}
              </div>
            </div>
            {errorMessage && <ErrorMessage text={errorMessage} />}
          </div>
        );
      }}
      {...props}
    />
  );
};

const MemoizedTextarea = memo(Textarea);
export default MemoizedTextarea;
