import './index.scss';

import Label from 'components/forms/Label';
import { HTMLAttributes, ReactNode, useId } from 'react';
import { Controller, useFormContext } from 'react-hook-form';

// ----------------------------------------------------------------------

export type ToggleProps = {
  name: string;
  value?: string | number;
  isToggled?: boolean;
  onToggle?: () => void;
  label?: ReactNode;
  required?: boolean;
  hasContext?: boolean;
  options: { value: string | number; label: string }[];
} & HTMLAttributes<HTMLInputElement>;

export const ContextToggle = ({ name, id, label, required, options }: ToggleProps) => {
  const { control } = useFormContext();

  return (
    <Controller
      control={control}
      name={name}
      render={({ field: { value, onChange } }) => (
        <div className="qb-switch">
          {label && <Label text={label} required={required} />}
          <input
            name="name"
            id={id}
            type="checkbox"
            checked={value === options[1].value}
            onChange={(e) => {
              const newValue = e.target.checked ? options[1].value : options[0].value;
              onChange(newValue);
            }}
            className="qb-switch__form"
          />
          <label className="qb-switch__actions" htmlFor={id}>
            <span className="qb-switch__label qb-switch__label--left">{options?.[0].label}</span>
            <span className="qb-switch__label qb-switch__label--right">{options?.[1].label}</span>
          </label>
        </div>
      )}
    />
  );
};

const Toggle = ({
  name,
  isToggled,
  onToggle,
  label,
  required,
  options,
  hasContext = false
}: ToggleProps) => {
  const id = useId();

  return (
    <>
      {hasContext ? (
        <ContextToggle name={name} id={id} label={label} required={required} options={options} />
      ) : (
        <div className="qb-switch">
          {label && <Label text={label} required={required} />}
          <input
            name="name"
            type="checkbox"
            id={id}
            checked={isToggled}
            onChange={onToggle}
            className="qb-switch__form"
          />
          <label className="qb-switch__actions" htmlFor={id}>
            <span className="qb-switch__label qb-switch__label--left">{options?.[0].label}</span>
            <span className="qb-switch__label qb-switch__label--right">{options?.[1].label}</span>
          </label>
        </div>
      )}
    </>
  );
};

export default Toggle;
